.navbar {
  width: 100%;
  margin-bottom: 40px;
}
.img-container {
  width: 250px;
  height: 150px;
  margin: 5px;
  overflow: auto;
}

.img-container img {
  width: 100%;
  height: 100%;
  float: left;
}
.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.title {
  width: 100%;
  text-align: center;
  margin-bottom: 50px;
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

